<template>
	<view class="activity-order">
		<view class="title-status">
			<view class="title">活动单编号：E20191106201633024700003</view>
			<view class="status" :style="{'color':order.IsOver?'#FEB042':'#00D08F'}">{{order.IsOver?'已结束': '进行中'}}
			</view>
		</view>
		<view class="activity">
			<image :src="order.Image?`httpsuni.navigateTo://farm.juzhentech.com${order.Image}`:''" mode=""></image>
			<view class="">
				<view class="address">{{order.Title}}</view>
				<view class="activity-time">活动时间：{{order.ActivityTime}}</view>
				<view class="rest-time">
					<text
						class="rest">{{order.RemainingTime>0?'剩余时间':'过期时间'}}：{{parseInt(time / 1000 / 60 / 60 % 24)}}时{{parseInt(time / 1000 / 60 % 60)}}秒</text>
					<text class="time" v-if="order.IsOver === false">{{order.RemainingTime}}</text>
				</view>
			</view>
		</view>
		<view class="activity-code">
			<view class="code" @click="handleCode">活动码</view>
		</view>
		<!-- <view class="activity-code-dialog" v-if="codeDialog">
			<activity-code-dialog @onHide="handleHide"></activity-code-dialog>
		</view> -->
		<uni-popup ref="popup" type="center">
			<!-- <activity-code-dialog v-if="codeDialog" @onHide="handleHide"></activity-code-dialog> -->
			<view class="popup">
				<view class="title">活动码</view>
				<!-- <view class="qrcode">
					<ay-qrcode ref="ayqrcode" :modal="modal_qr" :url="url" @hideQrcode="hideQrcode" :height="160"
						:width="160"></ay-qrcode>
				</view> -->
				<view class="qrcode">
					<ayQrcode ref="qrcode" :modal="modal_qr" :url="url" @hideQrcode="hideQrcode" :height="160"
						:width="160" />
				</view>
				<view class="status">{{order.Status===0?'待使用': '已使用'}}</view>
				<view class="close" @click="handleHide">关闭</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import ActivityCodeDialog from './components/activity-code-dialog/index.vue'
	import AyQrcode from "@/components/ay-qrcode/ay-qrcode.vue"
	export default {
		components: {
			ActivityCodeDialog,
			AyQrcode
		},
		props: {
			order: {
				default: {}
			},
			time: {
				default: {}
			}
		},
		data() {
			return {
				//二维码相关参数
				modal_qr: false,
				url: '',
				// url: 'https://uniapp.dcloud.net.cn/api/media/image.html', // 要生成的二维码值
				h: 0,
				m: 0
			}
		},
		methods: {
			handleCode() {
				this.$refs.popup.open()
				this.url = this.order.SignID
				this.showQrcode()
				let time = Math.abs(this.order.RemainingTime)
				let h = parseInt(time / 1000 / 60 / 60 % 24)
				let m = parseInt(time / 1000 / 60 % 60)
			},
			handleHide() {
				this.$refs.popup.close()
			},
			showQrcode() {
				let _this = this;
				this.modal_qr = true;
				console.log('活动码外层')
				setTimeout(function() {
					console.log('活动码')
					_this.$refs.qrcode.crtQrCode()
				}, 50)
			},
			// 展示二维码
			// showQrcode() {
			// 	let that = this
			// 	that.modal_qr = true;
			// 	// uni.showLoading()
			// 	console.log('活动码外层')
			// 	setTimeout(function() {
			// 		console.log('活动码')
			// 		// uni.hideLoading()
			// 		that.$refs.qrcode.crtQrCode()
			// 	}, 50)
			// },

			//传入组件的方法
			hideQrcode() {
				this.modal_qr = false;
			},
		}
	}
</script>

